@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
	font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
	font-family: 'Poppins', sans-serif;
}
  

body {
  background-color: #1c768f;
}

.App {
  background-color: #032539;
  min-height: 500px;
  width: 400px;
  margin: 15vh auto;
  border-radius: 8px;
  padding-bottom: 10px;
  transition: ease-in-out;
}

.App h1 {
  max-width: 100%;
  color: lightgrey;
  font-size: 150%;
  text-align: center;
  padding: 18px 20px 0 20px;
  margin: 0;
}

.App header #to-do-form input {
  background-color: #fbf3f2;
  border: 0;
  color: gray;
  width: 50%;
  height: 50px;
  margin: 20px;
  padding: 0 20px;
  font-size: 18px;
  border-radius: 10px;
}

.App header #to-do-form input::-webkit-input-placeholder {
  color: gray;
}

.App header #to-do-form input:-ms-input-placeholder {
  color: gray;
}

.App header #to-do-form input::-ms-input-placeholder {
  color: gray;
}

.App header #to-do-form input::placeholder {
  color: gray;
}

.App header #to-do-form input:focus {
  outline: none;
}

.App header #to-do-form button {
  height: 50px;
  width: 22%;
  border-radius: 5px;
  border: 0;
  background-color: #fa991c;
  color: #032539;
  cursor: pointer;
  font-weight: bold;
}

.App header #to-do-form button:focus {
  outline: none;
}

@media screen and (max-width: 400px) {
  .App {
    border-radius: 0px;
    width: 100%;
    height: 100vh;
    margin: 0 0;
  }
  .App h1 {
    font-size: 120%;
  }
}
